import React, { Component } from 'react';
import Coverflow from 'react-coverflow';
import styled from 'styled-components';
import SliderItem from './components/SliderItem';
import PERSONS from './constants/Persons';
import pirates from './pirates.jpeg';

const Wrapper = styled.div`
  height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
  background: url(${pirates}) center no-repeat;
  background-size: cover;

  > div {
    width: 100%;
  }

  div {
    outline: none;
  }

  div[class*="container"] {
    background: transparent;
    overflow: visible;
  }

  div[class*="__coverflow__"] {
    padding-bottom: 40px;
  }

  div[class*="coverflow__actions__"] {
    position: fixed;
    bottom: 20px;
  }

  button[class*="coverflow__button"] {
    background: #CDB380;
    border-color: #CDB380;
    color: #031634;

    &:hover {
      background: #9e8759;
      border-color: #9e8759;
    }
  }
`;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      active: 0,
      persons: PERSONS,
    };
  }

  render() {
    return (
      <Wrapper>
        <Coverflow ref="coverflow"
          width={'auto'}
          height={'600'}
          displayQuantityOfSide={2}
          navigation={true}
          enableHeading={false}
          active={this.state.active}
        >
          {this.state.persons.map((person) => {
            return <SliderItem
              name={person.name}
              image={person.image}
              color={person.color}
              text={person.text}
              key={person.id} />
          })}
        </Coverflow>
      </Wrapper>
    );
  }

  _handleClick() {
    let num = Math.floor((Math.random() * 10) + 1);
    this.setState({
      active: num
    });
  }

}

export default App;
